@use 'global' as *;

/*//////////////////////////////////////
背景色のアニメーション後に画像を表示する
/////////////////////////////////////*/

.js-slideColor {
	overflow: hidden;
}
.js-slideColor .is-view {
	position: absolute;
	top: 0;
	right: 0;
	width: 100%;
	height: 100%;
	z-index: 100;
	background-color: $brown;
}

