/* 縦線 */
.accordion__title::before {
  background: #ffffff;
  content: "";
  width: 1.75rem;
  height: 0.0625rem;
  position: absolute;
  top: 50%;
  right: 1rem;
  transform: rotate(0deg);
  transition: transform 0.3s ease-in-out;
}
@media screen and (min-width: 768px) {
  .accordion__title::before {
    width: 2rem;
    right: 2.5rem;
  }
}

/* 横線 */
.accordion__title::after {
  background: #ffffff;
  content: "";
  width: 1.75rem;
  height: 0.0625rem;
  position: absolute;
  right: 1rem;
  top: 50%;
  transition: opacity 0.3s ease-in-out;
  opacity: 0;
}
@media screen and (min-width: 768px) {
  .accordion__title::after {
    width: 2rem;
    right: 2.5rem;
  }
}

/* 縦線(クリック後) */
.accordion__title.open::before {
  transform: rotate(90deg);
}

/* 横線(クリック後) */
.accordion__title.open::after {
  opacity: 1;
}

