@use 'global' as *;

.box__container {
	display: flex;
}
.box__container.box__container--sub {
	flex-direction: column-reverse;
}
.box__wrapper {
	display: flex;
	flex-direction: column;
	@include mq(md) {
		flex-direction: row;
		align-items: flex-end;
		justify-content: space-between;
	}
}
.box__header {
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: calc((149 / 313) * 100%);
	border-bottom: my-rem(1) solid $brown;
	padding-bottom: my-rem(1);
	padding: my-rem(10);
	@include mq(md) {
		margin-top: my-rem(28);
		width: 100%;
		max-width: calc((276 / 472) * 100%);
	}
}
.box__header.box__header--sub {
	margin-top: my-rem(14);
	border-bottom: initial;
	padding-bottom: initial;
	max-width: initial;
	@include mq(md) {
		margin-top: my-rem(16);
	}
}
.box__gender {
	font-family: $jp-font-family1;
	font-size: my-rem(12);
	font-weight: $regular;
	line-height: calc(18 / 12);
}

.box__label {
	padding: my-rem(2) my-rem(11);
	border: my-rem(1) solid $brown;
	font-family: $jp-font-family1;
	font-weight: $regular;
	font-size: my-rem(12);
	line-height: calc(22 / 12);
	color: $brown;
	width: 100%;
	width: fit-content;
	margin-left: initial;
	@include mq(md) {
		padding: my-rem(5) my-rem(15);
		margin-left: auto;
	}
}

.box__title {
	margin-top: my-rem(4);
	font-family: $jp-font-family1;
	font-size: my-rem(16);
	font-weight: $medium;
	@include mq(md) {
		margin-top: my-rem(8);
		font-size: my-rem(24);
	}
}

.box__title--sub {
	margin-top: my-rem(8);
	font-size: my-rem(24);
}

.box__image {
	position: relative;
	margin-left: my-rem(12);
	width: 100%;
	max-width: calc((151 / 313) * 100%);

	@include mq(md) {
		margin-left: my-rem(16);
		max-width: calc((180 / 472) * 100%);
	}
}
.box__image::before {
	content: '';
	display: block;
	padding-top: calc((117 / 151) * 100%);
	@include mq(md) {
		padding-top: calc((140 / 180) * 100%);
	}
}

.box__image.box__image--sub {
	max-width: initial;
	margin-left: initial;
}
.box__image--sub::before {
	padding-top: calc((218 / 294) * 100%);
	@include mq(md) {
		padding-top: calc((216 / 294) * 100%);
	}
}
.box__image img {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.box__meta {
	margin-top: my-rem(15);
	@include mq(md) {
		margin-top: my-rem(40);
	}
}
