@use 'global' as *;

.footer {
	background-color: $brown;
	padding: my-rem(48) 0 my-rem(56);
	@include mq(md) {
		padding: my-rem(80) 0;
	}
}

.footer__inner {
	display: flex;
	flex-direction: column;
	max-width: my-rem(500);
	@include mq(md) {
		max-width: initial;
	}
}

.footer__top-container {
	display: flex;
	flex-direction: column;

	@include mq(md) {
		flex-direction: row;
	}
}

.footer__logo {
	width: my-rem(120);
	@include mq(md) {
		width: my-rem(200);
	}
}

.footer__sns-block {
	display: flex;
	flex-direction: row;
	align-items: flex-end;
	margin-top: my-rem(16);
	gap: my-rem(16);
	@include mq(md) {
		margin-left: my-rem(40);
		margin-top: initial;
		padding-bottom: my-rem(4);
	}
}

.footer__sns-facebook,
.footer__sns-instagram {
	display: inline-block;
	width: my-rem(24);
	height: my-rem(24);
	@include mq(md) {
		width: my-rem(32);
		height: my-rem(32);
	}
}

.footer-menu {
	margin-top: my-rem(37);
	width: 100%;
	margin-right: auto;
	margin-left: auto;
	display: flex;
	@include mq(md) {
		margin-top: my-rem(23);
		max-width: initial;
		margin-right: initial;
		margin-left: initial;
	}
}

.footer-menu__container-left {
	width: 100%;
	max-width: 53%;
	padding-left: my-rem(25);
	@include mq(md) {
		display: flex;
		width: 100%;
		max-width: calc((540 / 1080) * 100%);
	}
}
.footer-menu__container-right {
	width: 100%;
	max-width: 47%;
	padding-left: my-rem(25);
	margin-left: my-rem(2);
	@include mq(md) {
		margin-left: my-rem(26);
		display: flex;
		width: 100%;
		max-width: calc((540 / 1080) * 100%);
	}
}
.footer-menu__sub-container--left1 {
	@include mq(md) {
		width: 100%;
		max-width: calc((290 / 540) * 100%);
	}
}
.footer-menu__sub-container--right1 {
	@include mq(md) {
		width: 100%;
		max-width: calc((250 / 540) * 100%);
	}
}
.footer-menu__sub-container--left2 {
	@include mq(md) {
		width: 100%;
		max-width: calc((280 / 540) * 100%);
	}
}
.footer-menu__sub-container--right2 {
	@include mq(md) {
		width: 100%;
		max-width: calc((220 / 540) * 100%);
	}
}

.footer-menu__contents:not(:first-child) {
	margin-top: my-rem(38);
	@include mq(md) {
		margin-top: my-rem(38);
	}
}

.footer-menu__list-name {
	font-size: my-rem(16);
	font-family: $jp-font-family1;
	font-weight: $bold;
	line-height: calc(18 / 16);
	color: $white;
	position: relative;
}
.footer-menu__list-name::before {
	content: '';
	display: inline-block;
	width: my-rem(18);
	height: my-rem(18);
	background-image: url(../images/common/mug.webp);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	position: absolute;
	left: my-rem(-24);
}

.footer-menu__list-name a {
	display: block;
	width: 100%;
}
.footer-menu__list-name a:hover {
	opacity: 0.7;
	transition: 0.3s;
}
.footer-menu__list-name--space {
	margin-top: my-rem(38);
	@include mq(md) {
		margin-top: initial;
	}
}

.footer-menu__items {
	margin-top: my-rem(26);
	@include mq(md) {
		margin-top: my-rem(24);
	}
}

.footer-menu__item {
	margin-top: my-rem(23);
	font-size: my-rem(16);
	font-family: $jp-font-family1;
	font-weight: $regular;
	color: $white;
	line-height: calc(18 / 16);
	width: 100%;
	@include mq(md) {
		margin-top: my-rem(24);
	}
}
.footer-menu__item a {
	display: block;
}
.footer-menu__item a:hover {
	opacity: 0.7;
	transition: 0.3s;
}
.footer-menu__space {
	padding-right: my-rem(22);
	@include mq(md) {
		padding-right: initial;
	}
}

.footer__copyright {
	margin-top: my-rem(40);
	text-align: center;
	@include mq(md) {
		margin-top: my-rem(80);
	}
}

.footer__copyright-text {
	font-size: my-rem(12);
	font-family: $en-font-family1;
	font-weight: $regular;
	line-height: calc(18 / 12);
	color: $white;
	@include mq(md) {
		font-size: my-rem(14);
		line-height: calc(18 / 14);
	}
}
