@use 'global' as *;

.sub-contact {
	position: relative;
}

.sub-contact__decoration {
	position: absolute;
	top: my-rem(-25);
	left: my-rem(15);
	width: my-rem(120);
	height: my-rem(55);
	z-index: -1;

	@include mq(md) {
		top: my-rem(-71);
		width: my-rem(301);
		height: my-rem(138);
		left: calc(50% - min(47.9166vw, 38.75rem));
	}
}

.sub-contact__inner.inner {
	width: 100%;
	max-width: my-rem(500);

	@include mq(md) {
		max-width: my-rem(750);
	}
}

.sub-contact__content-error {
	color: $red;
	display: none; //require判定で表示
}

.sub-contact__content-error.is-show {
	display: block;
	display: flex;
	justify-content: center;
}

.sub-contact__content-errorText {
	font-family: $jp-font-family1;
	font-size: my-rem(20);
	font-weight: $bold;
}

.sub-contact__content-errorText--md-show {
	display: block;
	text-align: center;

	@include mq(md) {
		display: none;
	}
}

.sub-contact__form {
	margin-top: my-rem(38);
}
