@use 'global' as *;

.archive__container {
	margin-top: my-rem(24);
	width: fit-content;
}

.archive__items {
	color: $brown;
	font-family: $jp-font-family1;
	font-size: my-rem(24);
	font-weight: $normal;
	padding-left: my-rem(6);
	width: fit-content;
}

.archive__item {
	color: $brown;
	font-family: $jp-font-family1;
	font-size: my-rem(24);
	font-weight: $normal;
	width: fit-content;
}

.archive__sub-items {
	color: $brown;
	font-family: $jp-font-family1;
	font-size: my-rem(24);
	font-weight: $normal;
	width: fit-content;
}

.archive__sub-items.close {
	display: none;
}

.archive__sub-item {
	color: $brown;
	font-family: $jp-font-family1;
	font-size: my-rem(24);
	font-weight: $normal;
	padding-left: my-rem(24);
	display: block;
	width: fit-content;
}

.archive__item--newLayout,
.archive__item--oldLayout,
.archive__sub-item--layout {
	cursor: pointer;
}

.archive__item--newLayout::before,
.archive__item--oldLayout::before,
.archive__sub-item--layout::before {
	content: '';
	display: inline-block;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: my-rem(10) 0 my-rem(10) my-rem(17.3);
	border-color: transparent transparent transparent $brown;
	margin-right: my-rem(10);
}

