@use 'global' as *;

.information__inner {
	max-width: my-rem(600);
	width: 100%;
}
.information__container {
	margin-top: my-rem(26);
	display: flex;
	flex-direction: column;
	@include mq(md) {
		margin-top: my-rem(40);
		flex-direction: row;
	}
}

.information__image {
	position: relative;
	@include mq(md) {
		max-width: calc((540 / 1080) * 100%);
		width: 100%;
	}
}

.information__image::before {
	content: '';
	display: block;
	padding-top: calc((227 / 345) * 100%);
	@include mq(md) {
		padding-top: calc((356 / 540) * 100%);
	}
}

.information__image img {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.information__body {
	margin-top: my-rem(25);
	@include mq(md) {
		margin-top: my-rem(61);
		margin-left: my-rem(48);
		max-width: calc((492 / 1080) * 100%);
		width: 100%;
	}
}

.information__header {
	padding-bottom: my-rem(3);
	border-bottom: my-rem(1) solid $brown;
	@include mq(md) {
		padding-bottom: my-rem(7);
	}
}

.information__sub-title {
	font-size: my-rem(20);
	font-family: $jp-font-family1;
	font-weight: $bold;
	line-height: calc(24 / 20);
	@include mq(md) {
		font-size: my-rem(24);
		line-height: calc(24 / 24);
	}
}

.information__meta {
	margin-top: my-rem(8);
}

.information__button {
	text-align: center;
	margin-top: my-rem(40);
}
